import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import checkIcon from '../SignUp/icons/check.png';
import useClasses from './style';
import {
    Box,
    Paper,
    Button,
    Typography,
    InputLabel,
  } from '@material-ui/core'
import auth from 'API-Service/auth';
import { useToasts } from 'react-toast-notifications';
import { ClutchIcon } from 'views/ResetPassword/icons/icon';

function ForgotPassword() {
  const classes = useClasses();
  const { addToast } = useToasts();
  const [showError, setShowError] = useState(false);
  const [email, setEmail] = useState('');
  const [step, setStep] = useState(0);

  function validateEmail(mail) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
      setShowError(false)
      return (true)
    } else {
      setShowError(true)
    }
    return (false)
  }

  const handleEmail = (e) => {
    setEmail(e.target.value);
    validateEmail(e.target.value);
  }

  const handleResetPassword = () => {
    auth
      .forgot(email)
      .then(response => {
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){setStep(1)}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      }) 
  }

  return (
    <div className={classes.root}>
      {
        step == 0 ?
        <Paper className={classes.loginFormCard}
          style={{margin: "0px"}}
        >
          <Typography className={classes.title}> Forget Password? </Typography>
          <Typography className={classes.description}>No worries, we all make mistakes. Fill out your email and we will send you a link to reset your password.</Typography>
          <form noValidate autoComplete="off"
            style={{margin: "2px"}}
          >
            <Box className={showError ? classes.errorBox : classes.inputGroup}>
              <InputLabel className={classes.inputLabel}> Email</InputLabel>
              <input 
                type="email"
                id=""
                defaultValue=""
                placeholder="Enter your email.."
                helperText=""
                onChange={(e) => handleEmail(e)}
                className={classes.input}
                value={email}
              />
              { 
                showError && <Typography className={classes.error}> Email is not correct </Typography>
              }
            </Box>
            <Box>
              <Button fullWidth
                className={classes.loginBtn}
                disabled={showError || email.length == 0}
                style={{textTransform: "capitalize"}}
                onClick={handleResetPassword}
              >
                Reset your password
              </Button>
              
            </Box>
          </form>
        </Paper>
        :
        <Paper className={classes.resetFormCard}
          style={{textAlign: 'center'}}
        >
          <ClutchIcon style={{marginBottom: 24}}/>
          <Typography className={classes.title} style={{marginBottom: 8}}> Reset link has been sent! </Typography>
          <Typography className={classes.description_reset}>An email with your reset password link has been sent to <b>{email}</b> The link will expire in one hour</Typography>
        </Paper>
      }
      
      <Box>
        <Typography className={classes.title1}
          style={{marginTop: "2rem", display: 'flex'}}
        >
          Go back to
          <Link to='/login' className={classes.link}> Log In </Link>
        </Typography>
      </Box>
    </div>
  )
}

export default ForgotPassword